.container {
    z-index: 1;
    width: 100%;
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
    margin-top: 1%;
    
  }
  .content{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: top;
      margin-top: 3%;
  }
  .content_2{
    display: flex;
    flex-direction: row;
    margin-top: 1%;
  }

  .content_down{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 1%;
  }
  p {
    overflow: hidden;
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-word;
    font-weight: 100;
    font-style: normal;
    font-family: "Kumbh Sans", sans-serif;
    color: #2C2A2A;
    font-size: 24px;
    letter-spacing: -0.6px;
    line-height: 1.3;
  }

  .span_p{
    font-weight: 100;
    font-style: normal;
    font-family: "Kumbh Sans", sans-serif;
    color: #2C2A2A;
    font-size: 22px;
    letter-spacing: -0.6px;
    line-height: 1.3;
  }
  .span_bold{
    font-weight: 300;
    font-style: normal;
    font-family: "Kumbh Sans", sans-serif;
    color: #2C2A2A;
    font-size: 22px;
    letter-spacing: -0.6px;
    line-height: 1.3;
  }
  .span_red{
    font-weight: 400;
    font-style: normal;
    font-family: "Kumbh Sans", sans-serif;
    color: #FA0F3E;
    font-size: 22px;
    letter-spacing: -0.6px;
    line-height: 1.3;
    /* animation-name: textexample;
    animation-duration: 3s; */

  }
  /* @keyframes textexample {
    from {color: #FA0F3E;}
    to {color: #2C2A2A;}
  } */

