}

.navbar-container {
  height: 100px;
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: start;
  list-style: none;
  text-align: center;
}

.nav-item {
  height: 40px;
  border-bottom: 2px solid transparent;
}


.nav-links:hover {
  color: #FA0F3E;
}

.nav-links {
  color: #2C2A2A;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-weight: 400;
  font-style: normal;
  font-family: "Abhaya Libre", serif;
  font-size: 1.68rem;
  letter-spacing: -1.6px;
  line-height: 1.2;
  padding: 0 1.5rem 0 0;
  height: 100%;
}
.nav-links--active{
  font-weight: 600;
  color: #FA0F3E;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* height: 90vh; */
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    /* background: #1c2237; */
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: right;
    padding-bottom: 0.5rem;
    padding-right: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #FA0F3E;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
    height: 50%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #2C2A2A;
    font-size: 2rem;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
}
:root{
    --primary: #fff;

}

.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}

.btn--primary{
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);

}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3 ease-out;
}

.btn--wide{
    padding: 12px 64px;
    font-size: 20px;

}

.btn--large:hover, .btn--medium:hover, .btn--mobile:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;

}

.btn--wide:hover{
    color: #fff;
    background-color: #f00946;
    transition: all 0.2s ease-out;
}

